// Arquivo: src/components/Sidebar.js
import React from 'react';
import { Link } from 'react-router-dom';
import '../Sidebar.css';

function Sidebar({ isOpen, closeSidebar }) {
    return (
        // A classe 'open' ou '' ser adicionada dinamicamente
        // O CSS usar isso para mostrar/esconder com animao
        <nav className={`sidebar ${isOpen ? 'open' : ''}`}>
            {/* Boto para fechar o sidebar */}
            <button onClick={closeSidebar} className="close-button" aria-label="Fechar menu">
            </button>

            {/* Lista de Links de Navegao */}
            <ul>
                <li>
                    {/* Ao clicar no link, tambm chama closeSidebar para fechar o menu */}
                    <Link to="/" onClick={closeSidebar}>Minha Pokdex</Link>
                </li>
                <li>
                    {/* Link para Comparar (movido para c) */}
                    <Link to="/compare" onClick={closeSidebar}>Comparar Pokdex</Link>
                </li>
                {/* Adicione mais links aqui se precisar no futuro */}
            </ul>
        </nav>
    );
}

export default Sidebar;